import React from "react";
import { FaAward } from "react-icons/fa6";
import { AiFillProject } from "react-icons/ai";
import { GrUserExpert } from "react-icons/gr";
import '../../index.css'

const exp = [
    {
    id:1,
    jobExp:"3+ Years of Job",
    description: "Experience",
    icon: <FaAward />
},
{
    id:2,
    jobExp:"3+ Projects",
    description: "Completed",
    icon: <AiFillProject/>
},
{
    id:3,
    jobExp:"1 Company",
    description: "Employee",
    icon: <GrUserExpert />
},
]

export default function experience(){
    return(
        <div className="mt-[-10px] experince-cont container mx-auto w-3/5  flex justify-center items-center bg-gray-100 p-4 rounded-full drop-shadow-2x shadow-[0_20px_50px_rgba(8,_112,_184,_0.7)]">
        <div className="h-20 border-l-2 border-emerald-400"></div>
         {exp.map(data =>  <div className="flex justify-center">
          <div className="font-medium text-sm m-6 flex items-center">
            <button className="bg-sky-200 text-emerald-900 p-3 rounded-full mr-4">{data.icon}</button>
            <div className="justify-center">
              <p >{data.jobExp} </p>
              <span className="text-emerald-400">{data.description}</span>
            </div>
          </div>
          <div className="h-20 border-l-2 border-emerald-400"></div>
        </div>)}
      </div>
    )
}