import React, { useState } from "react";
import { Link, useLocation } from 'react-router-dom';
import { routesComponents } from '../../routes/routesComponets';
import logo from '../../assets/porofolio_logo.png'
export default function TopNavigation() {
    const location = useLocation();
    const [isOpen, setIsOpen] = useState(false);
    const toggleMenu = () => {
        setIsOpen(!isOpen);
    };
    return (
        <nav className="container mx-auto bg-white-200 p-4">
            <div className="max-w-7xl mx-auto px-4">
                <div className="flex justify-between items-center">
                    <div className="lg:pl-9 md:pl-9">
                    <img className=" bg-sky-200 rounded-lg h-10 shadow-2xl" src={logo} alt='profile-img' />
                    </div>
                    <div className="hidden md:flex space-x-4">
                        {routesComponents.map((route) => (
                            <Link
                                key={route.path}
                                to={route.path}
                                className={`text-emerald-900 font-medium rounded-lg px-4 py-2 ${location.pathname === route.path ? 'bg-emerald-400' : 'hover:bg-emerald-400'
                                    }`}
                            >
                                {route.name}
                            </Link>
                        ))}
                    </div>
                    <div className="md:hidden">
                        <button className="text-white" onClick={toggleMenu}>
                            <svg
                                className="text-emerald-900 h-6 w-6"
                                fill="none"
                                viewBox="0 0 24 24"
                                stroke="currentColor"
                            >
                                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M4 6h16M4 12h16M4 18h16" />
                            </svg>
                        </button>
                    </div>
                </div>
                {isOpen && (
                    <div className="md:hidden mt-2">
                        {routesComponents.map((route) => (
                            <Link
                                key={route.path}
                                to={route.path}
                                className="block font-medium text-emerald-900 hover:bg-emerald-400 text-emerald-900 rounded-lg p-2"
                                onClick={toggleMenu}
                            >
                                {route.name}
                            </Link>
                        ))}
                    </div>
                )}
            </div>
        </nav>
    )
}